<template>
  <div class="mmld">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child">
      <span v-show="showR">实&ensp;&ensp;时</span>
      <span v-show="!showR">历&ensp;&ensp;史</span>
      <i class="option_pull" @click="showRDM=true" v-if="!showRDM"></i>
      <div class="items" v-show="showRDM">
        <contract-list class="item" v-model="Contract" :Product="$route.query.pz">
          选合约
        </contract-list>
        <a class="item" @click="showR=true;showRDM=false" v-if="!showR">
          实&ensp;&ensp;时&ensp;<i class="option_pull_1"></i>
        </a>
        <a class="item" @click="showR=false;showRDM=false" v-if="showR">
          历&ensp;&ensp;史&ensp;<i class="option_pull_1"></i>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mmld',
  data() {
    return {
      BuySellR: {},
      BuySellH: {},
      Contract: '',
      Intervalid: 0,
      showR: true,
      showRDM: false,
    };
  },
  destroyed() {
    clearInterval(this.Intervalid);
  },
  methods: {
    startGetBuySellR(value) {
      if (value) {
        clearInterval(this.Intervalid);
        this.Intervalid = setInterval(() => this.GetBuySellR(value), 60000);
      }
    },
    async GetBuySellR(Contract) {
      const data = await this.ajax.BuySellR([Contract]);
      this.BuySellR = data.d;
    },
    async GetBuySellH(Contract) {
      const data = await this.ajax.BuySellH([Contract]);
      this.BuySellH = data.d;
    },
    RchartOption() {
      let Option = {};
      if (this.BuySellR.value) {
        Option = {
          color: [
            '#f65c5c', '#f9fdc3', '#fda635',
          ],
          title: {
            text: `${this.Contract}分时买卖力度`,
            x: 'center',
            y: 15,
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: { type: 'shadow' },
            formatter(params) {
              let res = `时间 :${params[0].name}`;
              for (let i = 0, l = params.length; i < l; i += 1) {
                res += `<br/>${params[i].seriesName}: ${Math.abs(params[i].value)}`;
              }
              return res;
            },
          },
          legend: {
            data: ['买开', '买平', '买换手', '卖开', '卖平', '卖换手'],
            y: 50,
          },
          xAxis: [
            {
              type: 'category',
              data: this.format.timeL(this.BuySellR.timeL),
              axisTick: {
                alignWithLabel: true,
              },
              axisLabel: {
                color: '#000',
              },
              axisLine: {
                lineStyle: {
                  color: this.LineColor,
                },
              },
            },
          ],
          yAxis: [
            {
              type: 'value',
              axisLabel: {
                formatter(value) {
                  let ret;
                  if (value > 0) ret = `${Math.abs(value)}`;
                  else if (value < 0) ret = `${Math.abs(value)}`;
                  else ret = Math.abs(value);
                  return ret;
                },
                color: '#000',
              },
              nameTextStyle: {
                color: '#000',
              },
              axisLine: {
                lineStyle: {
                  color: this.LineColor,
                },
              },
              splitLine: {
                lineStyle: {
                  color: this.LineColor,
                  type: 'dashed',
                },
              },
              z: 100,
            },
            {
              name: '买            ',
              type: 'value',
              position: 'left',
            },
            {
              name: '卖            ',
              type: 'value',
              position: 'left',
              nameLocation: 'start',
            },
          ],
          series: [
            {
              name: '卖换手',
              type: 'bar',
              stack: '买卖力度',
              data: [],
            },
            {
              name: '卖平',
              type: 'bar',
              stack: '买卖力度',
              data: [],
            },
            {
              name: '卖开',
              type: 'bar',
              stack: '买卖力度',
              data: [],
            },
            {
              name: '买换手',
              type: 'bar',
              stack: '买卖力度',
              data: [],
            },
            {
              name: '买平',
              type: 'bar',
              stack: '买卖力度',
              data: [],
            },
            {
              name: '买开',
              type: 'bar',
              stack: '买卖力度',
              data: [],
            },
          ],
        };
        Option.series[0].data = this.BuySellR.value.st;
        Option.series[1].data = this.BuySellR.value.sc;
        Option.series[2].data = this.BuySellR.value.so;
        Option.series[3].data = this.BuySellR.value.bt.map(x => -x);
        Option.series[4].data = this.BuySellR.value.bc.map(x => -x);
        Option.series[5].data = this.BuySellR.value.bo.map(x => -x);
      }
      return Option;
    },
    HchartOption() {
      let Option = {};
      if (this.BuySellH.value) {
        Option = {
          color: ['#fda635', '#f65c5c', '#c072d6', '#1dcacc'],
          legend: {
            data: ['总买平/总卖平比', '总买开/总卖开比', '总买换手/总卖换手比', '总买量/总卖量'],
            y: 50,
          },
          title: {
            text: `${this.Contract}历史走势`,
            x: 'center',
            y: 15,
          },
          tooltip: {
            trigger: 'axis',
            formatter(params) {
              let res = `时间 :${params[0].name}`;
              for (let i = 0, l = params.length; i < l; i += 1) {
                res += `<br/>${params[i].seriesName} : ${params[i].value}`;
              }
              return res;
            },
          },
          xAxis: [{
            type: 'category',
            data: this.format.dateL(this.BuySellH.dateL),
            axisLabel: {
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
          }],
          yAxis: [{
            type: 'value',
            scale: true,
            axisLabel: {
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
            splitLine: {
              lineStyle: {
                color: this.LineColor,
                type: 'dashed',
              },
            },
          }],
          series: [{
            name: '总买平/总卖平比',
            type: 'line',
            smooth: true,
            data: this.BuySellH.value.cr,
            symbol: 'circle',
          }, {
            name: '总买开/总卖开比',
            type: 'line',
            smooth: true,
            data: this.BuySellH.value.or,
            symbol: 'rect',
          }, {
            name: '总买换手/总卖换手比',
            type: 'line',
            smooth: true,
            data: this.BuySellH.value.bst,
            symbol: 'triangle',
          }, {
            name: '总买量/总卖量',
            type: 'line',
            smooth: true,
            data: this.BuySellH.value.bsv,
            symbol: 'diamond',
          }],
        };
      }
      return Option;
    },
  },
  computed: {
    chartOption() {
      let Option;
      if (this.showR) {
        Option = this.RchartOption();
      } else {
        Option = this.HchartOption();
      }
      return Option;
    },
  },
  watch: {
    Contract(value) {
      this.GetBuySellR(value);
      this.startGetBuySellR(value);
      this.GetBuySellH(value);
    },
  },
};
</script>



