<template>
  <div class="DateChoice">
    <mt-datetime-picker
        ref="picker"
        v-model="tdate"
        type="date"
        @confirm="handleConfirm">
    </mt-datetime-picker>
    <span @click="open()">{{DateFormat}}&ensp;<i class="option_pull_1"></i></span>
  </div>
</template>

<script>
export default {
  name: 'DateChoice',
  data() {
    return {
      date: this.value,
      tdate: this.value,
    };
  },
  props: {
    value: {
      default: '',
    },
    offsets: {
      default: 0,
    },
  },
  mounted() {
    this.GetData();
  },
  methods: {
    async GetData() {
      const data = await this.ajax.ServTime();
      this.date = new Date(data.d.lastDate.substring(0, 4),
        parseInt(data.d.lastDate.substring(4, 6), 10) - 1,
        data.d.lastDate.substring(6));
      this.tdate = new Date(data.d.lastDate.substring(0, 4),
        parseInt(data.d.lastDate.substring(4, 6), 10) - 1,
        data.d.lastDate.substring(6));
      this.date = new Date(this.date - (this.offsets * 24 * 60 * 60 * 1000));
      this.tdate = new Date(this.tdate - (this.offsets * 24 * 60 * 60 * 1000));
    },
    open() {
      this.$refs.picker.open();
    },
    handleConfirm() {
      this.date = this.tdate;
    },
  },
  computed: {
    DateFormat() {
      let ret = '';
      if (this.date) {
        ret = this.moment(this.date).format('YYYY - MM - DD');
      }
      return ret;
    },
  },
  watch: {
    date() {
      this.$emit('input', this.DateFormat);
    },
  },
};

</script>



