<template>
  <div class="fdgz">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child">
      <product-list v-model="Products">品种叠加</product-list>
    </div>
    <div class="Cdate">
      <date-choice v-model="bDate" offsets="120" id="bDate"></date-choice>
      <span>—</span>
      <date-choice v-model="eDate" id="eDate"></date-choice>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';

export default {
  name: 'fdgz',
  data() {
    return {
      Products: [this.$route.query.pz],
      RangeList: {},
      bDate: '',
      eDate: '',
      showRDM: false,
    };
  },
  methods: {
    async GetRangeList() {
      if (this.Products && this.bDate && this.eDate) {
        const data = await this.ajax.RangeList(this.Products, this.bDate, this.eDate);
        this.RangeList = data.d;
      }
    },
  },
  computed: {
    chartOption() {
      let Option;
      const vue = this;
      if (vue.RangeList.pc) {
        Option = {
          xAxis: {
            type: 'category',
            data: vue.format.dateL(vue.RangeList.dateL),
            axisLabel: {
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: vue.LineColor,
              },
            },
          },
          yAxis: [
            {
              type: 'value',
              axisLabel: {
                color: '#000',
              },
              axisLine: {
                lineStyle: {
                  color: vue.LineColor,
                },
              },
              splitLine: {
                lineStyle: {
                  color: vue.LineColor,
                  type: 'dashed',
                },
              },
            },
          ],
          series: [],
          legend: {
            top: 20,
            data: [],
          },
          grid: {
            top: '60',
            left: '100',
            bottom: '66',
            right: '30',
          },
        };
        // eslint-disable-next-line
        vue.RangeList.pc.forEach((pz, index) => {
          const series = {
            name: `${pz}`,
            data: vue.RangeList.value[pz].map(x => (x * 100).toFixed(2)),
            type: 'line',
            symbol: 'line',
            symbolSize: 0,
            color: vue.colors[index % 3].color,
            areaStyle: {
              normal: {
                color: vue.colors[index % 3].icolor,
              },
            },
          };
          Option.legend.data.push(`${pz}`);
          Option.series.push(series);
        });
      }
      return Option;
    },
  },
  watch: {
    Products() {
      if (this.bDate > this.eDate && this.bDate && this.eDate) {
        Toast('日期输入有误');
        return;
      }
      this.GetRangeList();
    },
    bDate() {
      if (this.bDate > this.eDate && this.bDate && this.eDate) {
        Toast('日期输入有误');
        return;
      }
      this.GetRangeList();
    },
    eDate() {
      if (this.bDate > this.eDate && this.bDate && this.eDate) {
        Toast('日期输入有误');
        return;
      }
      this.GetRangeList();
    },
  },
};
</script>



