<template>
  <div class="cdkc">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child one">
      <product-list v-model="Products">叠加品种</product-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cdkc',
  data() {
    return {
      title: '仓单库存',
      WareHouse: {},
      Products: [this.$route.query.pz],
    };
  },
  created() {
    this.GetData(this.Products);
  },
  methods: {
    async GetData(Products) {
      const data = await this.ajax.WareHouse(Products);
      this.WareHouse = data.d;
    },
  },
  computed: {
    chartOption() {
      const Option = {
        xAxis: {
          type: 'category',
          data: this.format.dateL(this.WareHouse.dateL),
          axisLabel: {
            color: '#000',
          },
          axisLine: {
            lineStyle: {
              color: this.LineColor,
            },
          },
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
            splitLine: {
              lineStyle: {
                color: this.LineColor,
                type: 'dashed',
              },
            },
          },
        ],
        series: [],
        legend: {
          top: 20,
          data: [],
        },
      };
      if (this.WareHouse.pc) {
        // eslint-disable-next-line
        this.WareHouse.pc.forEach((pz, index) => {
          const unit = this.WareHouse.unit[index];
          const series1 = {
            name: `${pz}${unit}`,
            data: this.WareHouse.value[pz],
            type: 'line',
            symbol: 'line',
            symbolSize: 0,
            color: this.colors[index % 3].color,
            areaStyle: {
              normal: {
                color: this.colors[index % 3].icolor,
              },
            },
          };
          Option.legend.data.push(`${pz}${unit}`);
          Option.series.push(series1);
        });
      }
      return Option;
    },
  },
  watch: {
    Products(value) {
      this.GetData(value);
    },
  },
};
</script>



