<template>
  <div class="ContractList">
    <span @click="showPopup=!showPopup"><slot></slot></span>
    <mt-popup v-model="showPopup" position="right" class="app">
      <div class="header">
        <div class="left" @click="showPopup=!showPopup">
            <i class="icon-left"></i>
        </div>
        <span class="title">选合约</span>
        <div class="right text" @click="showPopup=!showPopup">
          确定
        </div>
      </div>
      <div class="chose-wrapper">
        <div class="options">
          <div class="item2"
               v-for="item in options"
               :key="item"
               :class="contract===item? 'active' : ''"
               @click="contract=item">
            {{productinfo.n}}<br>
            {{item}}
          </div>
          <div v-if="options.length%5!==0"
               class="item2" v-for="x in new Array(5-options.length%5)" :key="x">
          </div>
        </div>
      </div>
    </mt-popup>
  </div>
</template>

<script>
export default {
  name: 'ContractList',
  data() {
    return {
      List: {},
      showPopup: false,
      contract: this.value,
      product: this.Product,
    };
  },
  props: {
    value: {
      default: '',
    },
    Product: {
      default: '',
    },
  },
  mounted() {
    this.GetData();
  },
  methods: {
    async GetData() {
      const data = await this.ajax.ProductList();
      this.List = data.d;
    },
    SetContract(contract) {
      this.contract = contract;
      this.$emit('input', contract);
    },
  },
  watch: {
    showPopup() {
      this.$emit('input', this.contract);
    },
  },
  computed: {
    options() {
      let ret = [];
      /* eslint-disable guard-for-in */
      for (let mt in this.List.pcl) {
        mt = this.List.pcl[mt];
        for (let Product in mt) {
          Product = mt[Product];
          if (Product.pc.toUpperCase() === this.product.toUpperCase()) {
            ret = Product.c.map(x => x.toUpperCase());
            this.SetContract(Product.cm.toUpperCase());
          }
        }
      }
      /* eslint-enable guard-for-in */
      return ret;
    },
    productinfo() {
      let ret = {};
      /* eslint-disable guard-for-in */
      for (let mt in this.List.pcl) {
        mt = this.List.pcl[mt];
        for (let Product in mt) {
          Product = mt[Product];
          if (Product.pc.toUpperCase() === this.product.toUpperCase()) {
            ret = Product;
          }
        }
      }
      /* eslint-enable guard-for-in */
      return ret;
    },
  },
};

</script>



