<template>
  <div class="dhcc">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child">
      <span v-show="showB=='0'">多排名</span>
      <span v-show="showB=='1'">空排名</span>
      <i class="option_pull" @click="showRDM=true" v-if="!showRDM"></i>
      <div class="items" v-show="showRDM">
        <a class="item" @click="showB='0';showRDM=false" v-if="showB=='1'">多排名&nbsp;
          <i class="option_pull_1"></i></a>
        <a class="item" @click="showB='1';showRDM=false" v-if="showB=='0'">空排名&nbsp;
          <i class="option_pull_1"></i></a>
      </div>
    </div>
    <div class="Cdate"><date-choice v-model="date" class="item"></date-choice></div>
  </div>
</template>

<script>
export default {
  name: 'dhcc',
  data() {
    return {
      BigHold: {},
      Product: [this.$route.query.pz],
      date: null,
      showB: '0',
      showRDM: false,
    };
  },
  methods: {
    async GetData() {
      const data = await this.ajax.BigHold(this.Product, this.date);
      this.BigHold = data.d;
    },
  },
  computed: {
    chartOption() {
      let Option = {};
      if (this.BigHold.value) {
        let l;
        let b;
        let s;
        if (this.showB === '0') {
          l = this.BigHold.value.bl || [];
          if (this.BigHold.value.bd) {
            b = this.BigHold.value.bd.b || [];
            s = this.BigHold.value.bd.s || [];
          } else {
            b = [];
            s = [];
          }
        } else {
          l = this.BigHold.value.sl || [];
          if (this.BigHold.value.sd) {
            b = this.BigHold.value.sd.b || [];
            s = this.BigHold.value.sd.s || [];
          } else {
            b = [];
            s = [];
          }
        }
        Option = {
          grid: {
            top: '80',
            left: '90',
            bottom: '70',
            right: '30',
          },
          xAxis: {
            type: 'category',
            data: l.map(x => x.replace(/(.{2})/, '$1\n')),
            axisLabel: {
              interval: 0,
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
          },
          yAxis: [
            {
              type: 'value',
              axisLabel: {
                color: '#000',
              },
              axisLine: {
                lineStyle: {
                  color: this.LineColor,
                },
              },
              splitLine: {
                lineStyle: {
                  color: this.LineColor,
                  type: 'dashed',
                },
              },
            },
          ],
          series: [{
            name: '多',
            data: b,
            stack: 1,
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#f65c5c',
              },
            },
          }, {
            name: '空',
            data: s.map(x => -x),
            stack: 1,
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#4cd8b9',
              },
            },
          }],
        };
      }
      return Option;
    },
  },
  watch: {
    date() {
      this.GetData();
    },
  },
};
</script>



