<template>
  <div class="ProductList">
    <span @click="showPopup=!showPopup"><slot></slot></span>
    <mt-popup v-model="showPopup" position="right" class="app">
      <div class="header">
        <div class="left" @click="showPopup=!showPopup">
          <i class="icon-left"></i>
        </div>
        <span class="title">品种叠加</span>
        <div class="right text" @click="showPopup=!showPopup">
          确定
        </div>
      </div>
      <div class="chose-wrapper">
        <div class="select-info">
          <div class="left">
            <div class="item" v-for="item in this.List.mt"
                 :key="item"
                 :class=" mt===item ? 'active' : '' "
                 @click="mt=item">
              {{MTlist[item]}}
            </div>
          </div>
          <div class="right">
            <input v-model="filterSTR" placeholder="请输入查询内容">
            <i class="fa fa-search"></i>
          </div>
        </div>
        <div class="selected">
          <div class="item"
               v-for="product in Nproducts"
               @click="ChooseProducts(product.pc)"
               :key="product.pc">
            <i class="box_close"></i>
            {{product.n}}
          </div>
        </div>
        <div class="options">
          <div class="item"
               v-for="product in options"
               @click="ChooseProducts(product.pc)"
               :class="InProducts(product.pc) ? 'active' : ''"
               :key="product.pc">
            {{product.n}}
          </div>
          <div v-if="options.length%6!==0" class="item"
               v-for="x in new Array(6-options.length%6)" :key="x">
          </div>
        </div>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';

export default {
  name: 'ProductList',
  data() {
    return {
      List: {},
      showPopup: false,
      max: 3,
      products: this.value,
      mt: 'SHFE',
      MTlist: {
        SHFE: '上期',
        INE: '能源',
        DCE: '大商',
        CZCE: '郑商',
        CFFEX: '中金',
      },
      filterSTR: '',
    };
  },
  props: {
    value: {
      default: {},
    },
  },
  mounted() {
    this.GetData();
  },
  methods: {
    async GetData() {
      const data = await this.ajax.ProductList();
      this.List = data.d;
    },
    InProducts(value) {
      return this.products.indexOf(value) !== -1;
    },
    ChooseProducts(value) {
      if (value === this.$route.query.pz) {
        Toast({
          message: '不能取消主品种',
          position: 'bottom',
          duration: 500,
        });
        return;
      }
      const tmp = this.products.slice(0);
      const index = tmp.indexOf(value);
      if (index === -1) {
        if (tmp.length < 3) tmp.push(value);
        else {
          Toast({
            message: '最多选3个',
            position: 'bottom',
            duration: 500,
          });
        }
      } else {
        tmp.splice(index, 1);
      }
      this.products = tmp;
    },
  },
  watch: {
    showPopup() {
      this.$emit('input', this.products);
    },
  },
  computed: {
    options() {
      if (!this.List.pcl) return [];
      const ret = [];
      const mt = this.List.pcl[this.mt] || [];
      mt.forEach((value) => {
        const str = value.n + value.pc + value.pc.toUpperCase();
        if (str.indexOf(this.filterSTR) !== -1) {
          ret.push({
            n: value.n,
            pc: value.pc.toUpperCase(),
          });
        }
      });
      return ret;
    },
    Nproducts() {
      if (!this.List.pcl) return [];
      const ret = [];
      for (let mt in this.List.pcl) {
        if ({}.hasOwnProperty.call(this.List.pcl, mt)) {
          mt = this.List.pcl[mt];
          mt.forEach((value) => {
            const index = this.products.indexOf(value.pc.toUpperCase());
            if (index !== -1) {
              ret[index] = {
                n: value.n,
                pc: value.pc.toUpperCase(),
              };
            }
          });
        }
      }
      return ret;
    },
  },
}
;

</script>



