// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import Router from 'vue-router';
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css';
import moment from 'moment';

import ajax from '@/components/js/ajax';
import format from '@/components/js/format';
import color from '@/components/js/color';

import echarts from '@/components/echarts';
import ProductList from '@/components/ProductList';
import ContractList from '@/components/ContractList';
import MemberList from '@/components/MemberList';
import DateChoice from '@/components/DateChoice';

import App from './App';
import router from './router';

Vue.config.productionTip = false;
Vue.prototype.ajax = ajax;
Vue.prototype.format = format;
Vue.prototype.moment = moment;
Vue.prototype.colors = color.colors;
Vue.prototype.LineColor = color.LineColor;

Vue.use(Router);
Vue.use(Mint);

Vue.component('echarts', echarts);
Vue.component('ProductList', ProductList);
Vue.component('ContractList', ContractList);
Vue.component('MemberList', MemberList);
Vue.component('DateChoice', DateChoice);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
});



