<template>
  <div class="clfxchart">
    <div class="legend">
      <svg height="10px" width="10px">
        <path d="M 10 10 L 0 10 L 5 0 z" fill="#f65c5c">
        </path>
      </svg>
      开多
      <svg height="10px" width="10px">
        <path d="M 0 0 L 10 0 L 5 10 z" fill="#ffffff" stroke="#4cd8b9">
        </path>
      </svg>
      平多
      <svg height="10px" width="10px">
        <path d="M 0 0 L 10 0 L 5 10 z" fill="#4cd8b9">
        </path>
      </svg>
      开空
      <svg height="10px" width="10px">
        <path d="M 10 10 L 0 10 L 5 0 z" fill="#ffffff" stroke="#f65c5c">
        </path>
      </svg>
      平空
    </div>
    <echarts :Option='chartOption' ref="echarts"></echarts>
  </div>
</template>

<script>
export default {
  name: 'clfxchart',
  data() {
    return {
      title: this.$route.name,
      TradeMsg: {},
      Products: [this.$route.query.pz],
    };
  },
  created() {
    this.GetData(this.Products);
  },
  methods: {
    async GetData(Products) {
      const data = await this.ajax.TradeMsg(Products);
      this.TradeMsg = data.d;
    },
  },
  computed: {
    chartOption() {
      const Option = {
        tooltip: {
          trigger: 'axis',
          formatter: params =>
            `${params[0].marker + params[0].name}<br>
             高：${params[0].value[4]}<br>
             开：${params[0].value[1]}<br>
             低：${params[0].value[3]}<br>
             收：${params[0].value[2]}`,
        },
        xAxis: {
          type: 'category',
          data: this.format.dateL(this.TradeMsg.dateL),
          axisLabel: {
            color: '#000',
          },
          axisLine: {
            lineStyle: {
              color: this.LineColor,
            },
          },
        },
        yAxis: {
          scale: true,
          axisLabel: {
            color: '#000',
          },
          axisLine: {
            lineStyle: {
              color: this.LineColor,
            },
          },
          splitLine: {
            lineStyle: {
              color: this.LineColor,
              type: 'dashed',
            },
          },
        },
        series: [
          {
            name: 'K线',
            type: 'candlestick',
            itemStyle: {
              normal: {
                color: '#ff853b',
                color0: '#23a3ed',
                borderColor: '#ff853b',
                borderColor0: '#23a3ed',
              },
            },
            data: [],
          },
          {
            name: '策略',
            type: 'line',
            data: [],
            showAllSymbol: true,
            lineStyle: {
              normal: {
                color: 'rgba(0, 0, 0, 0)',
              },
            },
          },
        ],
      };
      const xhs = [
        {
          value: 3,
          symbolSize: 0,
        },
        // 开多
        {
          value: 3,
          symbol: 'path://M 300 300 L 100 300 L 200 100 z',
          symbolSize: 5,
          symbolOffset: [0, -10],
          itemStyle: {
            normal: {
              color: '#f65c5c',
            },
          },
        },
        // 平多
        {
          value: 3,
          symbol: 'path://M 100 100 L 300 100 L 200 300 z',
          symbolSize: 5,
          symbolOffset: [0, -10],
          itemStyle: {
            normal: {
              color: 'rgba(0, 0, 0, 0)',
              borderColor: '#4cd8b9',
            },
          },
        },
        // 开空
        {
          value: 2,
          symbol: 'path://M 100 100 L 300 100 L 200 300 z',
          symbolSize: 5,
          symbolOffset: [0, 10],
          itemStyle: {
            normal: {
              color: '#4cd8b9',
            },
          },
        },
        // 平空
        {
          value: 2,
          symbol: 'path://M 300 300 L 100 300 L 200 100 z',
          symbolSize: 5,
          symbolOffset: [0, 10],
          itemStyle: {
            normal: {
              color: 'rgba(0, 0, 0, 0)',
              borderColor: '#f65c5c',
            },
          },
        },
      ];
      /* eslint-disable guard-for-in */
      // 设置K线数据
      for (let pz in this.TradeMsg.pc) {
        pz = this.TradeMsg.pc[pz];
        Option.series[0].data = this.TradeMsg.value[pz];
      }
      // 设置信号数据
      const series1 = new Array(Option.xAxis.data.length);
      for (const index in Option.xAxis.data) {
        const date = Option.xAxis.data[index].replace(/\D/g, '').replace(/(.{4})(.{4})/g, '$2$1');
        const sis = this.TradeMsg.sis.find(value => value.date === date);
        if (sis) {
          const xh = sis.si[this.TradeMsg.sin.indexOf(this.title)];
          if (xh) {
            series1[index] = JSON.parse(JSON.stringify(xhs[xh]));
            const v = xh > 2 ? 2 : 3;
            series1[index].value = Option.series[0].data[index][v];
          }
        } else {
          series1[index] = {
            value: Option.series[0].data[index][0],
            symbolSize: 0,
          };
        }
      }
      Option.series[1].data = series1;
      /* eslint-enable guard-for-in */
      return Option;
    },
  },
  watch: {
    $route(value) {
      this.title = value.name;
    },
  },
};
</script>



