import echarts from 'echarts';

const color = ['rgba(246,92,92,0.7)', 'rgba(253,166,53,0.7)', 'rgba(192,114,214,0.7)'];
const colors = [];

color.forEach((value) => {
  colors.push(
    {
      color: value,
      icolor: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
        { offset: 0, color: value },
        { offset: 1, color: '#ffffff' },
      ]),
    },
  );
});

export default {
  colors,
  LineColor: '#FDA635',
};



