<template>
  <div class="hycc">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child one">
      <member-list v-model="Members">会员叠加</member-list>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts';

export default {
  name: 'hycc',
  data() {
    return {
      title: this.$route.name,
      MemberHold: {},
      Members: ['永安期货'],
      Product: [this.$route.query.pz],
    };
  },
  created() {
    this.GetData(this.Members);
  },
  methods: {
    async GetData(Members) {
      const data = await this.ajax.MemberHold(this.Product, Members);
      this.MemberHold = data.d;
    },
  },
  computed: {
    chartOption() {
      const Option = {
        xAxis: {
          type: 'category',
          data: this.format.dateL(this.MemberHold.dateL),
        },
        yAxis: [
          {
            type: 'value',
          },
        ],
        series: [],
        legend: {
          top: 38,
          itemWidth: 15,
          data: [],
        },
      };
      /* eslint-disable guard-for-in */
      for (const index in this.MemberHold.mList) {
        const mc = this.MemberHold.mList[index];
        const series1 = {
          name: `${mc}多`,
          data: this.MemberHold.value[index].b,
          type: 'line',
          color: '#f65c5c',
          areaStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: '#f65c5c' },
                { offset: 1, color: '#ffffff' },
              ]),
            },
          },
        };
        const series2 = {
          name: `${mc}空`,
          data: this.MemberHold.value[index].s,
          type: 'line',
          color: '#4cd8b9',
          areaStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: '#4cd8b9' },
                { offset: 1, color: '#ffffff' },
              ]),
            },
          },
        };
        Option.legend.data.push(`${mc}多`, `${mc}空`);
        Option.series.push(series1, series2);
      }
      /* eslint-enable guard-for-in */
      return Option;
    },
  },
  watch: {
    Members(value) {
      this.GetData(value);
    },
  },
};
</script>



