<template>
  <div class="MemberList">
    <span @click="showPopup=!showPopup"><slot></slot></span>
    <mt-popup v-model="showPopup" position="right" class="app">
      <div class="header">
        <div class="left" @click="showPopup=!showPopup">
          <i class="icon-left"></i>
        </div>
        <span class="title">会员叠加</span>
        <div class="right" @click="showPopup=!showPopup">
          确定
        </div>
      </div>
      <div class="chose-wrapper">
        <div class="select-info">
          <div class="left" style="border: 0">
          </div>
          <div class="right">
            <input v-model="filterSTR" placeholder="请输入查询内容">
            <i class="fa fa-search"></i>
          </div>
        </div>
        <div class="selected">
          <div class="item"
               v-for="product in members"
               @click="ChooseItem(product)"
               :key="product">
            <i class="fa fa-close"></i>
            {{product}}
          </div>
        </div>
        <div class="options">
          <div class="item"
               v-for="item in options"
               @click="ChooseItem(item)"
               :class="InMembers(item) ? 'active' : ''"
               :key="item">
            {{item}}
          </div>
          <div v-if="options.length%6!==0"
               class="item" v-for="x in new Array(6-options.length%6)" :key="x">
          </div>
        </div>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';

export default {
  name: 'MemberList',
  data() {
    return {
      List: {},
      showPopup: false,
      max: 3,
      members: this.value,
      filterSTR: '',
    };
  },
  props: {
    value: {
      default: '',
    },
  },
  mounted() {
    this.GetData();
  },
  methods: {
    async GetData() {
      const data = await this.ajax.MemberList();
      this.List = data.d;
    },
    InMembers(value) {
      return this.members.indexOf(value) !== -1;
    },
    ChooseItem(value) {
      const tmp = this.members.slice(0);
      const index = tmp.indexOf(value);
      if (index === -1) {
        if (tmp.length < 3) tmp.push(value);
        else {
          Toast({
            message: '最多选3个',
            position: 'bottom',
            duration: 500,
          });
        }
      } else if (tmp.length === 1) {
        Toast({
          message: '最少选1个',
          position: 'bottom',
          duration: 500,
        });
      } else {
        tmp.splice(index, 1);
      }
      this.members = tmp;
    },
  },
  watch: {
    showPopup() {
      this.$emit('input', this.members);
    },
  },
  computed: {
    options() {
      const ret = [];
      const mt = this.List.mList || [];
      mt.forEach((value) => {
        if (value.indexOf(this.filterSTR) !== -1) {
          ret.push(value);
        }
      });
      return ret;
    },
  },
};

</script>



