<template>
  <div class="zjbh">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child one">
      <product-list v-model="Products">品种叠加</product-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'zjbh',
  data() {
    return {
      CashChange: {},
      Products: [this.$route.query.pz],
    };
  },
  created() {
    this.GetData(this.Products);
  },
  methods: {
    async GetData(Products) {
      const data = await this.ajax.CashChange(Products);
      this.CashChange = data.d;
    },
  },
  computed: {
    chartOption() {
      const Option = {
        xAxis: {
          type: 'category',
          data: this.format.dateL(this.CashChange.dateL),
          axisLabel: {
            color: '#000',
          },
          axisLine: {
            lineStyle: {
              color: this.LineColor,
            },
          },
        },
        yAxis: [
          {
            type: 'value',
            nameTextStyle: {
              color: '#000',
            },
            axisLabel: {
              formatter: '{value}%',
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [],
        legend: {
          top: 20,
          data: [],
        },
      };
      if (this.CashChange.pc) {
        // eslint-disable-next-line
        this.CashChange.pc.forEach((pz, index) => {
          const series1 = {
            name: `${pz}比例`,
            // yAxisIndex: 1,
            data: this.format.CashChangeRatio(this.CashChange.value[pz]),
            type: 'line',
            symbol: 'line',
            symbolSize: 0,
            color: this.colors[index % 3].color,
            areaStyle: {
              normal: {
                color: this.colors[index % 3].icolor,
              },
            },
          };
          /* const series2 = {
            name: `${pz}额度`,
            yAxisIndex: 0,
            data: this.CashChange.value[pz].map(value => (value / 100000000).toFixed(2)),
            type: 'bar',
            color: this.colors[index % 3].color,
          };
          Option.legend.data.push(`${pz}比例`, `${pz}额度`);
          Option.series.push(series1, series2); */
          Option.legend.data.push(`${pz}比例`);
          Option.series.push(series1);
        });
      }
      return Option;
    },
  },
  watch: {
    Products(value) {
      this.GetData(value);
    },
  },
};
</script>



