<template>
  <div id="chart" ref='mychart'></div>
</template>

<script>
import echarts from 'echarts';

export default {
  name: 'echart',
  data() {
    return {
      allOption: {
        animation: false,
        grid: {
          top: '75',
          left: '100',
          bottom: '50',
          right: '30',
        },
        tooltip: {
          trigger: 'axis',
        },
      },
    };
  },
  props: {
    Option: {
      type: Object,
    },
  },
  mounted() {
    this.mychart = echarts.init(this.$refs.mychart);
    window.onresize = () => {
      this.mychart.resize();
    };
  },
  watch: {
    Option(val) {
      const DrawOption = Object.assign(this.allOption, val);
      this.mychart.setOption(DrawOption, true);
      this.mychart.resize();
    },
  },
};

</script>



