<template>
  <div class="dkcc">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child one">
      <product-list v-model="Products">品种叠加</product-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'dkcc',
  data() {
    return {
      title: this.$route.name,
      RaiseFall: {},
      Products: [this.$route.query.pz],
    };
  },
  created() {
    this.GetData(this.Products);
  },
  methods: {
    async GetData(Products) {
      const data = await this.ajax.RaiseFall(Products);
      this.RaiseFall = data.d;
    },
  },
  computed: {
    chartOption() {
      const Option = {
        xAxis: {
          type: 'category',
          data: this.format.dateL(this.RaiseFall.dateL),
          axisLabel: {
            color: '#000',
          },
          axisLine: {
            lineStyle: {
              color: this.LineColor,
            },
          },
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
        series: [],
        legend: {
          top: 20,
          data: [],
        },
      };
      if (this.RaiseFall.pc) {
        // eslint-disable-next-line
        this.RaiseFall.pc.forEach((pz, index) => {
          const series1 = {
            name: `${pz}`,
            data: this.RaiseFall.value[pz].map(x => Number(x).toFixed(2)),
            type: 'line',
            symbol: 'line',
            symbolSize: 0,
            color: this.colors[index % 3].color,
            areaStyle: {
              normal: {
                color: this.colors[index % 3].icolor,
              },
            },
          };
          Option.legend.data.push(`${pz}`);
          Option.series.push(series1);
        });
        const markLine = {
          name: 'markLine',
          type: 'line',
          data: [],
          markLine: {
            symbolSize: 0,
            lineStyle: { color: '#7f7f7f' },
            silent: true,
            data: this.RaiseFall.threshold.map((y) => {
              const ret = {
                yAxis: y,
                label: {
                  color: y > 0 ? '#f65c5c' : '#4cd8b9',
                },
              };
              return ret;
            }),
          },
        };
        Option.series.push(markLine);
      }

      return Option;
    },
  },
  watch: {
    Products(value) {
      this.GetData(value);
    },
  },
};
</script>



