<template>
  <div class="bdfx">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child">
      <span v-show="showRate=='0'">波动值</span>
      <span v-show="showRate=='1'">波动率</span>
      <i class="option_pull" @click="showRDM=true" v-if="!showRDM"></i>
      <div class="items" v-if="showRDM">
        <product-list class="item" v-model="Products">品种叠加</product-list>
        <div class="item" @click="showRate='0';showRDM=false" v-if="showRate=='1'">波动值
          <i class="option_pull_1"></i></div>
        <div class="item" @click="showRate='1';showRDM=false" v-if="showRate=='0'">波动率
          <i class="option_pull_1"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'bdfx',
  data() {
    return {
      title: this.$route.name,
      Products: [this.$route.query.pz],
      FluctuateList: {},
      showRate: '0',
      Intervalid: '',
      showRDM: false,
    };
  },
  created() {
    this.start(this.Products);
  },
  destroyed() {
    clearInterval(this.Intervalid);
  },
  methods: {
    start(value) {
      if (value) {
        this.GetFluctuateList(value);
        clearInterval(this.Intervalid);
        this.Intervalid = setInterval(() => this.GetFluctuateList(value), 60000);
      }
    },
    async GetFluctuateList(Products) {
      const data = await this.ajax.FluctuateList(Products);
      this.FluctuateList = data.d;
    },
  },
  computed: {
    chartOption() {
      let Option;
      if (this.FluctuateList.pc) {
        Option = {
          xAxis: {
            type: 'category',
            data: this.format.dateT(this.FluctuateList.dateT),
            axisLabel: {
              interval: 0,
              rotate: -20,
              color: '#000',
            },
            axisTick: {
              alignWithLabel: true,
            },
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
          },
          yAxis: [
            {
              type: 'value',
              axisLabel: {
                color: '#000',
              },
              axisLine: {
                lineStyle: {
                  color: this.LineColor,
                },
              },
              splitLine: {
                lineStyle: {
                  color: this.LineColor,
                  type: 'dashed',
                },
              },
            },
          ],
          series: [],
          legend: {
            top: 20,
            data: [],
          },
        };
        // eslint-disable-next-line
        this.FluctuateList.pc.forEach((pz, index) => {
          const series = {
            name: `${pz}`,
            data: [],
            type: 'line',
            symbol: 'line',
            symbolSize: 0,
            color: this.colors[index % 3].color,
            areaStyle: {
              normal: {
                color: this.colors[index % 3].icolor,
              },
            },
          };
          if (this.showRate === '0') {
            series.data = this.format.MaxAvg(this.FluctuateList.value[pz]);
          } else {
            series.data = this.format.MaxAvg(this.FluctuateList.rate[pz])
              .map(x => (x * 100).toFixed(2));
          }
          Option.legend.data.push(`${pz}`);
          Option.series.push(series);
        });
      }
      return Option;
    },
  },
  watch: {
    Products(value) {
      this.start(value);
    },
  },
};
</script>



