<template>
  <div class="zffx">
    <echarts :Option='chartOption' ref="echarts"></echarts>
    <div class="right-menu child one">
      <product-list v-model="Products">叠加品种</product-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'zffx',
  data() {
    return {
      title: this.$route.name,
      Products: [this.$route.query.pz],
      AmpList: {},
    };
  },
  created() {
    this.start(this.Products);
  },
  destroyed() {
    clearInterval(this.Intervalid);
  },
  methods: {
    start(value) {
      if (value) {
        this.GetAmpList(value);
        clearInterval(this.Intervalid);
        this.Intervalid = setInterval(() => this.GetAmpList(value), 60000);
      }
    },
    async GetAmpList(Products) {
      const data = await this.ajax.AmpList(Products);
      this.AmpList = data.d;
      this.AmpList.dateT.reverse();
      /* eslint-disable guard-for-in */
      for (let pz in this.AmpList.pc) {
        pz = this.AmpList.pc[pz];
        this.AmpList.value[pz].reverse();
      }
      /* eslint-enable guard-for-in */
    },
  },
  computed: {
    chartOption() {
      let Option;
      if (this.AmpList.pc) {
        Option = {
          xAxis: {
            type: 'category',
            data: this.AmpList.dateT.map(x => `${x}日`),
            boundaryGap: false,
            axisLabel: {
              color: '#000',
            },
            axisLine: {
              lineStyle: {
                color: this.LineColor,
              },
            },
          },
          yAxis: [
            {
              type: 'value',
              axisLabel: {
                color: '#000',
              },
              axisLine: {
                lineStyle: {
                  color: this.LineColor,
                },
              },
              splitLine: {
                lineStyle: {
                  color: this.LineColor,
                  type: 'dashed',
                },
              },
            },
          ],
          series: [],
          legend: {
            top: 20,
            data: [],
          },
        };
        // eslint-disable-next-line
        this.AmpList.pc.forEach((pz, index) => {
          const series = {
            name: `${pz}`,
            data: this.AmpList.value[pz].map(x => (x * 100).toFixed(2)),
            type: 'line',
            symbol: 'line',
            symbolSize: 0,
            color: this.colors[index % 3].color,
            areaStyle: {
              normal: {
                color: this.colors[index % 3].icolor,
              },
            },
          };
          Option.legend.data.push(`${pz}`);
          Option.series.push(series);
        });
      }
      return Option;
    },
  },
  watch: {
    Products(value) {
      this.start(value);
    },
  },
};
</script>



