<template>
  <div class="ccpm">
    <div class="qdata" v-if="HoldProduct.qDate">最新日期：
      {{HoldProduct.qDate
      .replace(/(.{4})/, '$1-')
      .replace(/(.{7})/, '$1-')}}
    </div>
    <div class="table-title">
      <div>成交排名</div>
      <div>买单排名</div>
      <div>卖单排名</div>
    </div>
    <div class="thead">
      <div><span>#</span><i>会员简称</i><i>成交量</i><i>增减</i></div>
      <div><i>会员简称</i><i>持买单量</i><i>增减</i></div>
      <div><i>会员简称</i><i>持卖单量</i><i>增减</i></div>
    </div>
    <div class="data" v-if="HoldProduct.value">
      <div class="col">
        <div class="row" v-for="(item,index) in HoldProduct.value.bargain" :key="index">
          <span>{{index+1}}</span>
          <i>{{item.n}}&ensp;</i><i v-show="item.v!=='0'">{{item.v}}</i>
          <i :class="[item.inc>0 ? 'red':'green']" v-show="item.v!=='0'">{{item.inc}}</i>
        </div>
      </div>
      <div class="col">
        <div class="row" v-for="(item,index) in HoldProduct.value.buyVolumn" :key="index">
          <i>{{item.n}}&ensp;</i><i v-show="item.v!=='0'">{{item.v}}</i>
          <i :class="[item.inc>0 ? 'red':'green']" v-show="item.v!=='0'">{{item.inc}}</i>
        </div>
      </div>
      <div class="col">
        <div class="row" v-for="(item,index) in HoldProduct.value.sellVolumn" :key="index">
          <i>{{item.n}}&ensp;</i><i v-show="item.v!=='0'">{{item.v}}</i>
          <i :class="[item.inc>0 ? 'red':'green']" v-show="item.v!=='0'">{{item.inc}}</i>
        </div>
      </div>
    </div>
    <div class="right-menu child one">
      <contract-list v-model="Contract" :Product="$route.query.pz">
        选合约
      </contract-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ccpm',
  data() {
    return {
      HoldProduct: {},
      Contract: '',
    };
  },
  created() {
  },
  methods: {
    async GetData(Contract) {
      const data = await this.ajax.HodeProduct([Contract]);
      this.HoldProduct = data.d;
    },
  },
  watch: {
    Contract(value) {
      this.GetData(value);
    },
  },
};
</script>



