/* global CONFIG */
import Router from 'vue-router';
import menu from '@/page/menu';

import zjbh from '@/page/zjfx/zjbh';
import mmld from '@/page/zjfx/mmld';

import ccpm from '@/page/ccfx/ccpm';
import dkcc from '@/page/ccfx/dkcc';
import hycc from '@/page/ccfx/hycc';
import dhcc from '@/page/ccfx/dhcc';

import bdfx from '@/page/zdfx/bdfx';
import zffx from '@/page/zdfx/zffx';
import fdgz from '@/page/zdfx/fdgz';

import clfxchart from '@/page/clfx/chart';

import cdkc from '@/page/cdkc/cdkc';

window.CONFIG = window.CONFIG || {};
const routerMode = CONFIG.mode || 'hash';
const router = new Router({
  mode: routerMode,
  routes: [
    {
      myname: '资金分析',
      path: '/zjfx',
      component: menu,
      children: [
        { path: '', redirect: 'zjbh' },
        {
          name: '资金变化',
          path: 'zjbh',
          component: zjbh,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '买卖力度',
          path: 'mmld',
          component: mmld,
          meta: {
            kf: true,
          },
        },
      ],
    },
    {
      myname: '持仓分析',
      path: '/ccfx',
      component: menu,
      children: [
        { path: '', redirect: 'ccpm' },
        {
          name: '持仓排名',
          path: 'ccpm',
          component: ccpm,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '多空持仓',
          path: 'dkcc',
          component: dkcc,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '会员持仓',
          path: 'hycc',
          component: hycc,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '大户持仓',
          path: 'dhcc',
          component: dhcc,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
      ],
    },
    {
      myname: '涨跌分析',
      path: '/zdfx',
      component: menu,
      children: [
        { path: '', redirect: 'fdgz' },
        {
          name: '幅度跟踪',
          path: 'fdgz',
          component: fdgz,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '振幅分析',
          path: 'zffx',
          component: zffx,
          meta: {
            kf: true,
          },
        },
        {
          name: '波动分析',
          path: 'bdfx',
          component: bdfx,
          meta: {
            kf: true,
          },
        },
      ],
    },
    {
      myname: '策略分析',
      path: '/clfx',
      component: menu,
      children: [
        { path: '', redirect: 'tqa' },
        {
          name: '唐安奇',
          path: 'tqa',
          component: clfxchart,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '三均线',
          path: 'sjx',
          component: clfxchart,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '布林轨道',
          path: 'blgd',
          component: clfxchart,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '单均线',
          path: 'djx',
          component: clfxchart,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
        {
          name: '红三兵',
          path: 'hsb',
          component: clfxchart,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
      ],
    },
    {
      myname: '仓单库存',
      path: '/cdkc',
      component: menu,
      children: [
        { path: '', redirect: 'cdkc' },
        {
          name: '仓单库存',
          path: 'cdkc',
          component: cdkc,
          meta: {
            kf: true,
            keepAlive: true,
          },
        },
      ],
    },
    { path: '*', redirect: '/zjfx' },
  ],
});

router.beforeEach((to, from, next) => {
  /* eslint-disable no-param-reassign */
  to.query.pz = to.query.pz || 'CU'; // 设置默认品种
  to.query.pz = to.query.pz.toUpperCase();
  /* eslint-enable no-param-reassign */
  next();
});

export default router;



