function dateL(data) {
  const ret = [];
  /* eslint-disable guard-for-in */
  for (const item in data) {
    const year = Object.keys(data[item])[0];
    for (const mmdd in data[item][year]) {
      ret.push(`${data[item][year][mmdd].replace(/(.{2})/, '$1-')}\n${year}`);
    }
  }
  /* eslint-enable guard-for-in */
  return ret;
}

function timeL(data) {
  const ret = [];
  /* eslint-disable guard-for-in */
  for (const date in data) {
    for (const time in data[date]) {
      ret.push(`${data[date][time].replace(/(.{2})/, '$1:')}`);
    }
  }
  /* eslint-enable guard-for-in */
  return ret;
}

function dateT(data) {
  const ret = [];
  /* eslint-disable guard-for-in */
  for (const index in data) {
    ret.unshift(`${data[index]}平均`);
    ret.unshift(`${data[index]}最大`);
  }
  /* eslint-enable guard-for-in */
  ret.push('当前');
  return ret;
}

function ratio(data) {
  let ret = data.map(parseFloat);
  const max = Math.max(...ret);
  ret = ret.map(value => ((value / max) * 100).toFixed(2));
  return ret;
}

function CashChangeRatio(data) {
  const ret = data.map(parseFloat);
  const retratio = [0];
  for (let i = 1; i < ret.length; i += 1) {
    const x = ret[i - 1] === 0 ? ret[i] : ret[i - 1];
    let temp = (ret[i] - ret[i - 1]) / x;
    temp = (temp * 100).toFixed(2);
    if (ret[i] === 0 || ret[i - 1] === 0) retratio.push(0);
    else retratio.push(temp);
  }
  return retratio;
}

function MaxAvg(data) {
  const ret = [];
  /* eslint-disable guard-for-in */
  for (const index in data.max) {
    ret.unshift(Number(data.avg[index]).toFixed(2));
    ret.unshift(Number(data.max[index]).toFixed(2));
  }
  ret.push(Number(data.d_c[0]).toFixed(2));
  /* eslint-enable guard-for-in */
  return ret;
}

export default {
  dateL,
  dateT,
  timeL,
  ratio,
  MaxAvg,
  CashChangeRatio,
};




