<template>
  <div class="menu">
    <div class="header">
      <div class="left">
        <i class="icon-left" @click="$router.go(-1)"></i>
        <i class="icon-home" @click="ajax.Home()"></i>
      </div>
      <span class="title">金融分析</span>
      <div class="right">
        <i class="icon-consultation" @click="showPopup=!showPopup"></i>
      </div>
    </div>
    <div class="wrapper">
      <div class="left-menu" @click="showRightMenu=false">
        <router-link class="item" v-for="links in $router.options.routes" :key="links.path"
                     v-if="links.myname!=null" :to="{ path: links.path ,query: $route.query}">
          {{links.myname.substr(0, 2)}}
        </router-link>
      </div>
      <keep-alive>
        <router-view class="view" v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view class="view" v-if="!$route.meta.keepAlive"></router-view>
      <div class="right-menu">
      <span @click="showRightMenu=true">
        {{$route.name}}<i v-if="prompt_dot($route.name)" class="prompt_dot"></i>
      </span>
        <i :class="option_pull_red?'option_pull_red':'option_pull'"
           v-if="!showRightMenu&&RList.length!==2"
           @click="showRightMenu=true">
          <i class="option_pull_red_animation"
             v-if="!showRightMenu&&RList.length!==2&&option_pull_red"
             @click="showRightMenu=true"></i>
        </i>
        <div class="items" v-if="showRightMenu&&RList.length!==2"
             @click="showRightMenu=false">
          <router-link class="item" v-for="item in RList" :key="item.path"
                       :to="{ name: item.name ,query: $route.query}"
                       v-if="item.name!==$route.name">
            <i v-if="prompt_dot(item.name)" class="prompt_dot"></i>{{ item.name }}
            <i class="option_pull_1"></i>
          </router-link>
        </div>
      </div>
    </div>
    <mt-popup v-model="showPopup" position="top" class="info">
      <div class="windows">
        <i class="popup_close" @click="showPopup=!showPopup"></i>
        <div class="title">{{$route.name}}</div>
        <div class="text">
          我是一个弹出框
        </div>
        <i class="pic popup_img"></i>
      </div>
    </mt-popup>
  </div>
</template>

<script>
export default {
  name: 'dhmenu',
  data() {
    return {
      showRightMenu: false,
      showPopup: false,
      xh: {
        BS: 0,
        sis: 0,
      },
    };
  },
  async created() {
    this.xh = await this.ajax.xh(this.$route.query.pz);
  },
  methods: {
    prompt_dot(name) {
      let ret = false;

      if (this.xh.BS !== 0 && name === '多空持仓') ret = true;
      if (this.xh.sis !== 0) {
        if (this.xh.sis[0] !== '0' && name === '唐安奇') ret = true;
        if (this.xh.sis[1] !== '0' && name === '三均线') ret = true;
        if (this.xh.sis[2] !== '0' && name === '单均线') ret = true;
        if (this.xh.sis[3] !== '0' && name === '布林轨道') ret = true;
        if (this.xh.sis[4] !== '0' && name === '红三兵') ret = true;
      }
      return ret;
    },
  },
  computed: {
    RList() {
      const ret = this.$router.options.routes.find(
        value => value.path === this.$route.matched[0].path,
      );
      return ret.children;
    },
    option_pull_red() {
      let ret = false;
      if (this.xh.BS === 1 && this.$route.path.indexOf('ccfx') !== -1) ret = true;
      if (this.xh.sis !== 0 && this.$route.path.indexOf('clfx') !== -1) ret = true;
      return ret;
    },
  },
};
</script>



